import React from 'react'
import { Route } from 'react-router'

import Overview from './Pages/Overview'

module.exports = class Index extends React.Component
{
	render()
	{
		return (
			<div>
				<Route exact path="/statistics" component={Overview} />
			</div>
		);
	}
}


